
h1 {
  text-align: center;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}
a {
  color: #38999A;
}

.vc-compact {
  padding-top: 5px;
  padding-left: 5px;
  width: 240px;
  border-radius: 2px;
  -webkit-box-shadow: 0 2px 10px rgba(0,0,0,.12), 0 2px 5px rgba(0,0,0,.16);
          box-shadow: 0 2px 10px rgba(0,0,0,.12), 0 2px 5px rgba(0,0,0,.16);
  background-color: #fff;
}
.vc-compact-colors {
  overflow: hidden;
  padding: 0;
  margin: 0;
}
.vc-compact-color-item {
  list-style: none;
  width: 15px;
  height: 15px;
  float: left;
  margin-right: 5px;
  margin-bottom: 5px;
  position: relative;
  cursor: pointer;
}
.vc-compact-color-item--white {
  -webkit-box-shadow: inset 0 0 0 1px #ddd;
          box-shadow: inset 0 0 0 1px #ddd;
}
.vc-compact-color-item--white .vc-compact-dot {
  background: #000;
}
.vc-compact-dot {
  position: absolute;
  top: 5px;
  right: 5px;
  bottom: 5px;
  left: 5px;
  border-radius: 50%;
  opacity: 1;
  background: #fff;
}

.vc-editable-input {
  position: relative;
}
.vc-input__input {
  padding: 0;
  border: 0;
  outline: none;
}
.vc-input__label {
  text-transform: capitalize;
}

.code {
    padding: 10px 20px;
    margin: 20px 0;
    background: #333;
    color: #eee;
    -webkit-transition: 0.2s all ease;
    transition: 0.2s all ease;
    display: block;
    border-radius: 5px;
    line-height: 1.8em;
    font-family: monospace;
    font-size: 1.5em;
    overflow: auto;
}

.code {
    padding: 10px 20px;
    margin: 20px 0;
    background: #333;
    -webkit-transition: 0.2s all ease;
    transition: 0.2s all ease;
    display: block;
    border-radius: 5px;
    line-height: 1.8em;
    font-family: monospace;
    font-size: 1.5em;
    overflow: auto;
}
.vc-compact {
    width: 500px;
}
.palette-popover {
    background-color: #fff;
}
.element {
    background-color: #FF4B48;
    border-radius: 2px;
    padding: 5px;
    margin: 5px;
    color: #000;
    display: inline-block;
    float: left;
    cursor: pointer;
}
.selected-element {
    background-color: #90DDF7;
    border-radius: 2px;
    padding: 5px;
    margin: 5px;
    color: #000;
    display: inline-block;
    float: left;
    cursor: pointer;
}
.element-picker {
    width: 45%;
    display: inline-block;
    padding-left: 20px;
}
.preview-code {
    width: 50%;
    float: left;
    display: inline-block;
}
.elements {
    margin: 10px;
    clear: left;
    display: block;
    overflow: auto;
    min-height: 50px;  /* must set min height to allow dragging components while list is empty */
    border: 1px #999999 solid;
    border-radius: 5px;
}
#selected-elements{
    display: block;
    clear: left;
    padding-top: 10px;
}
.etc {
    clear: left;
    display: block;
    margin: 10px;
    padding-top: 10px;
}
.trigger {
    float: left;
}
.button {
    background-color: #5DE071;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    border-radius: 2px;
    padding: 5px;
    margin: 5px;
    border: #000 solid 2px;
    font-size: 1em;
    font-weight: bold;
    cursor: pointer;
}
h3 {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
}
/**
 * prism.js default theme for JavaScript, CSS and HTML
 * Based on dabblet (http://dabblet.com)
 * @author Lea Verou
 */

code[class*="language-"],
pre[class*="language-"] {
	color: black;
	background: none;
	text-shadow: 0 1px white;
	font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	word-wrap: normal;
	line-height: 1.5;

	-moz-tab-size: 4;
	-o-tab-size: 4;
	tab-size: 4;

	-webkit-hyphens: none;
	-ms-hyphens: none;
	hyphens: none;
}

pre[class*="language-"]::-moz-selection, pre[class*="language-"] ::-moz-selection,
code[class*="language-"]::-moz-selection, code[class*="language-"] ::-moz-selection {
	text-shadow: none;
	background: #b3d4fc;
}

pre[class*="language-"]::selection, pre[class*="language-"] ::selection,
code[class*="language-"]::selection, code[class*="language-"] ::selection {
	text-shadow: none;
	background: #b3d4fc;
}

@media print {
	code[class*="language-"],
	pre[class*="language-"] {
		text-shadow: none;
	}
}

/* Code blocks */

pre[class*="language-"] {
	padding: 1em;
	margin: .5em 0;
	overflow: auto;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
	background: #f5f2f0;
}

/* Inline code */

:not(pre) > code[class*="language-"] {
	padding: .1em;
	border-radius: .3em;
	white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
	color: slategray;
}

.token.punctuation {
	color: #999;
}

.namespace {
	opacity: .7;
}

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
	color: #905;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
	color: #690;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
	color: #a67f59;
	background: hsla(0, 0%, 100%, .5);
}

.token.atrule,
.token.attr-value,
.token.keyword {
	color: #07a;
}

.token.function,
.token.class-name {
	color: #DD4A68;
}

.token.regex,
.token.important,
.token.variable {
	color: #e90;
}

.token.important,
.token.bold {
	font-weight: bold;
}

.token.italic {
	font-style: italic;
}

.token.entity {
	cursor: help;
}
